/* Inline #5 | http://localhost:3001/logs */

.logItem {
    width: 60vw;
    margin: 16px auto;
    background-color: white;
    padding: 16px;
    border-radius: 15px;
    direction: ltr;
}

.log-date {
    margin-bottom: 8px;
    color: green;
}

.log-msg {
    color: white;
    background-color: #e54b00;
    display: inline-block;
    padding: 8px;
    border-radius: 10px;
}

.logItem {
    /* width: 60vw; */
    width: max-content;
    max-width: 360px;
}

.toggle-obj {
    display: inline-block;
    margin-bottom: 4px;
    background-color: green;
    color: white;
    border-radius: 5px;
    padding: 4px 8px;
    cursor: pointer;
}

.obj {
    margin-top: 8px;
}

.object-item {
    width: max-content;
    max-width: 350px;
    margin: 8px auto;
    background-color: #e1e1e1;
    padding: 4px 8px;
    border-radius: 5px;
}